import request from '@/utils/request'

// 获取收货地址列表
export const getAddressList = () => {
  return request({
    url: '/address/list',
    method: 'get'
  })
}
// 获取省市区列表
export const getProvinceList = () => {
  return request({
    url: '/region/tree'
  })
}
// 添加地址
export const AddAddress = (name, phone, detail, array = []) => {
  return request({
    url: '/address/add',
    method: 'post',
    data: {
      form: {
        name,
        phone,
        detail,
        region: array
      }
    }
  })
}
// 修改地址
export const editAddress = (addressId, name, phone, detail, array = []) => {
  return request({
    url: '/address/edit',
    method: 'post',
    data: {
      addressId,
      form: {
        name,
        phone,
        detail,
        region: array
      }
    }
  })
}
// 删除地址
export const removeAddress = (addressId) => {
  return request({
    url: '/address/remove',
    method: 'post',
    data: {
      addressId
    }
  })
}

// 设置默认地址
export const setDefAddress = (addressId) => {
  return request({
    url: '/address/setDefault',
    method: 'post',
    data: {
      addressId
    }
  })
}

// 获取默认地址
export const getDefAddress = () => {
  return request({
    url: '/address/defaultId'
  })
}
// 获取地址详情
export const getAddressDetail = (addressId) => {
  return request({
    url: '/address/detail',
    params: { addressId }
  })
}
