<template>
    <div>
      <van-nav-bar
        title="支付页面"
        left-arrow
        @click-left="$router.go(-1)"
      />

      <div class="PayHead" @click="$router.push('/address')">
        <van-icon name="logistics" class="HeadIcon" size="20px"/>
          <div class="PayHeadInfo" v-if="AddressList.address_id">
            <span style="color: gray; font-size: 15px;">{{AddressList.name +'/'+ AddressList.phone}}</span>
            <span style="color: gray; font-size: 15px;">{{AddressList.region.province + AddressList.region.city + AddressList.region.region + AddressList.detail}}</span>
          </div>
          <div class="PayHeadInfo" v-else>
            <span style="color: gray; font-size: 15px;">请选择配送地址</span>
          </div>
          <span class="right">></span>
      </div>
      <div class="dotted-line"></div>

      <div class="PayBody" v-for="item in order.goodsList" :key="item.goods_id">
        <img :src="item.goods_image"  class="BodyImg">
        <div class="BodyInfo">
          <span class="BodyName">{{item.goods_name}}</span>
          <div class="Body">
            <span class="BodyCount">x{{item.total_num}}</span>
            <span class="BodyPrice">￥{{item.total_pay_price}}</span>
          </div>
        </div>
      </div>

      <div class="priceInfo">
        <div class="priceInfo2">
        共{{order.orderTotalNum}}件商品，合计  ￥<span class="AllPrice">{{order.orderPrice}}</span>
        </div>
      </div>

      <div class="payInfo">
        <div style="display: flex;">订单总金额：<span class="payInfoAllPrice">￥{{order.orderTotalPrice}}</span></div>
        <div style="display: flex;">优惠券：<span class="coupon">无优惠券可用</span></div>
        <div style="display: flex;">配送费用：<span class="payInfoAllPrice">+￥0.00</span></div>
      </div>

      <div class="PayWay">
        <div class="title">支付方式</div>
          <div class="PayWayInfo">
            <div>
              <van-icon name="balance-o" class="leftIcon" size="20px"/>
              <span> 余额支付 (可用￥{{personal.balance}}元)</span>
            </div>
          <van-icon name="passed" class="rightIcon" size="20px"/>
        </div>
      </div>

      <div class="message">
        <span class="messageInfo">
        选填：买家留言（50字内）
      </span>
        <textarea class="messageInp" v-model="message"></textarea>
      </div>

      <div class="PayFoot">
        <div class="FootLeft">
          <span>实付款：￥</span>
          <span style="color: red; font-weight: 600;">{{order.orderPayPrice}}</span>
        </div>
        <button class="FootRight" @click="submitClick">提交订单</button>
      </div>
    </div>
  </template>

<script>
import { getAddressDetail, getDefAddress } from '@/api/address'
import { getPayList, submitOrder } from '@/api/pay'
export default {
  name: 'LoginIndex',
  async created () {
    this.getAddressList()
    this.getGoodsList()
  },
  data () {
    return {
      defAddress: '',
      AddressList: [],
      message: '',
      order: {},
      personal: {}
    }
  },
  methods: {
    async getAddressList () {
      const res = await getDefAddress()
      if (res.data.defaultId) {
        this.defAddress = res.data.defaultId
        const addressInfo = await getAddressDetail(this.defAddress)
        // console.log(addressInfo)
        this.AddressList = addressInfo.data.detail
        console.log(this.AddressList)
      } else {
        return 0
      }
    },
    async getGoodsList () {
      if (this.mode === 'cart') {
        const res = await getPayList(this.mode, { cartIds: this.cartIds })
        this.order = res.data.order
        this.personal = res.data.personal
      } else {
        const res2 = await getPayList(this.mode,
          {
            goodsId: this.goodsId,
            goodsSkuId: this.goodsSkuId,
            goodsNum: this.goodsNum

          }
        )
        this.order = res2.data.order
        this.personal = res2.data.personal
      }
    },
    async submitClick () {
      if (this.mode === 'cart') {
        const res = await submitOrder(this.mode, {
          cartIds: this.cartIds,
          remark: this.message
        })
        console.log(res)
      } if (this.mode === 'buyNow') {
        await submitOrder(this.mode, {
          remark: this.message,
          goodsId: this.goodsId,
          goodsSkuId: this.goodsSkuId,
          goodsNum: this.goodsNum
        })
      }
      this.$toast.success('支付成功')
      this.$router.replace('/myorder')
    }
  },
  computed: {
    cartIds () {
      return this.$route.query.cartIds
    },
    mode () {
      return this.$route.query.mode
    },
    goodsId () {
      return this.$route.query.goodsId
    },
    goodsSkuId () {
      return this.$route.query.goodsSkuId
    },
    goodsNum () {
      return this.$route.query.goodsNum
    }
  }
}
</script>

  <style lang="less" scoped>
    .PayHead{
      display: flex;
      margin-top: 20px;
    }
    .PayHeadInfo{
      display: flex;
      flex-direction: column;
    }
    .HeadIcon{
      padding: 10px 10px;
    }
    .right{
      height: 20px;
      margin-left:auto;
      margin-right: 20px;
      margin-top: 10px;
      color: gray;
    }
    .dotted-line{
      width: 100%;
      height: 2px; /* 虚线的高度 */
      margin-top: 15px;
      background-image: repeating-linear-gradient(
        to right,
       yellow,
       red,
        transparent 50%,
        transparent 20%
        );
    }
    .PayBody{
      display: flex;
      margin-top: 15px;
    }
    .BodyInfo{
      max-width: 385px;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      margin-left: 20px;
      margin-right: 20px;
    }
    .BodyImg{
      width: 100px;
      margin-left: 20px;
    }
    .BodyName{
      margin-top: 15px;
      font-size: small;
      font-weight: 600;
      display: -webkit-box;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .Body{
      display: flex;
      margin-top: 20px;
    }
    .BodyCount{
      margin-right: auto;
    }
    .BodyPrice{
      margin-left: auto;
      color: red;
      font-weight: 600;
    }
    .priceInfo{
      display: flex;
      margin-top: 15px;
      margin-left: auto;
    }
    .priceInfo2{
      margin-left: auto;
      margin-right: 20px;
    }
    .AllPrice{
      margin-left: auto;
      color: red;
      font-weight: 600;
    }
    .payInfo{
      display: flex;
      flex-direction: column;
      margin-top: 20px;
      border-top: 1px solid gray;
      border-bottom: solid 1px gray;
      padding-top: 15px;
      div{
        margin-bottom: 20px;
      }
    }
    .payInfoAllPrice{
      margin-left: auto;
      margin-right: 10px;
      color: red;
      font-weight: 600;
    }
    .coupon{
      margin-left: auto;
      margin-right: 10px;
    }
    .PayWay{
      display: flex;
      margin-top: 20px;
      margin-bottom: 10px;
      margin-left: 10px;;
      margin-right: 10px;
      flex-direction: column;
      border-bottom: solid gray 1px;
      padding-bottom: 20px;
    }
    .PayWayInfo{
      display: flex;
      margin-top: 10px;
    }
    .leftIcon{
      margin-right: auto;
    }
    .rightIcon{
      margin-left: auto;
    }
    .message{
      display: flex;
      flex-direction: column;
    }
    .messageInfo{
      margin-top: 10px;
      margin-bottom: 5px;
      margin-left: 5px;
      font-size: small;
    }
    .messageInp{
      height: 100px;
      margin-left: 5px;
      margin-right: 5px;
      margin-bottom: 51px;
    }
    .PayFoot{
      display: flex;
      position: fixed;
      bottom: 0;
      width: 100%;
      background-color: white;
      align-items: center;
      padding-left: 10px;
    }
    .FootLeft{
      margin-right: auto;
    }
    .FootRight{
      margin-left: auto;
      border: none;
      background-color: red;
      color:white;
      width: 100px;
      height: 50px;
    }
  </style>
